import calcite from 'calcite-web'
import Store from './lib/store.js'
import most from 'most'
import Delegate from './lib/delegate.js'
import $ from './lib/$.js'

let modal = document.querySelector('.js-sharing-modal')
let delegate = Delegate(modal)

let state = {
  access: 'me', // 'org', 'public'
  groups: []
}

let store = Store('sharing-ui')
store(state)

// change access via select
let accesschange = delegate('change', '.js-sharing-select').map(e => e.target.value)

accesschange.observe(access => {
  state.access = access
  render()
})

// change groups in filter dropdown
let groupclick = delegate('click', '.js-sharing-group')
let groupremove = delegate('click', '.filter-dropdown-remove')
let groupchanges = groupclick.merge(groupremove)

groupchanges.observe(e => {
  let $groups = document.querySelectorAll('.filter-dropdown-active')
  state.groups = calcite.nodeListToArray($groups).map(el => el.textContent)
  render()
})

// update the ui when state changes
function render () {
  $('.js-sharing-loader').removeClass('is-active')
  let last = store()
  let dirty = (JSON.stringify(state) !== JSON.stringify(last))

  // assemble summary message
  let message = 'only you'

  if (state.access === 'org') {
    message = 'your organization'
  } else if (state.access === 'public') {
    message = 'everybody'
  }

  var punctuation = state.groups.length ? ' and the above groups.' : '.'
  message += punctuation
  document.querySelector('.js-sharing-summary').innerHTML = message

  // if accessible by groups, the public, or an org, show the social sharing ui
  $('.js-sharing-link').addClass('hide')
  if (state.access !== 'me' || state.groups.length) {
    if (last.groups.length || last.access !== 'me' ) {
      $('.js-sharing-link').removeClass('hide')
    }
  }

  // if you've changed settings, show save and cancel buttons
  if (dirty) {
    $('.js-sharing-action-bar').removeClass('hide')
  } else {
    $('.js-sharing-action-bar').addClass('hide')
  }
}

// save to local storage
delegate('click', '.js-sharing-save').observe(e => {
  $('.js-sharing-loader').addClass('is-active')
  store(state)
  window.setTimeout(render, 800);
})

// reset the state to whatever was in local storage
delegate('click', '.js-sharing-cancel').observe(e => {
  state = store()
  document.querySelector('.js-sharing-select').value = state.access
  // can't figure out how to rebuild the groups list, so groups don't reset on cancel.
  render()
})

